body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: -webkit-gradient(
  linear,
  left top, right top,
  from(paleturquoise), to(orange)
);
  background: -webkit-linear-gradient(
  left,
  paleturquoise, orange
);
  background: -o-linear-gradient(
  left,
  paleturquoise, orange
);
  background: linear-gradient(
  to right,
  paleturquoise, orange
);
}



